import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Modal from 'react-responsive-modal'
import withEvolution from '../providers/withEvolution'
import Button from '../Button'
import ClasseSlot from '../classes/ClasseSlot'
import {
  faCalendarAlt,
  faPencilAlt,
  faCircle,
  faClock,
} from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import CreditsManager from '../credits/CreditsManager'
import CreditsProgress from '../credits/CreditsProgress'
import SpecialOptatives from '../credits/SpecialOptatives'
import TrackStatus from '../credits/TrackStatus'

class ScheduleManager extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isModalOpen: false,
    }
  }

  openModal = () => {
    this.setState({ isModalOpen: true })
  }

  handleCloseModal = () => {
    this.setState({ isModalOpen: false })
  }

  render() {
    const { isModalOpen } = this.state
    const { doingClasses, doneClasses, scheduledClasses } = this.props

    return (
      <div className="pa1">
        <Button
          text="Ver Agenda"
          variation="important"
          icon={faCalendarAlt}
          onClick={this.openModal}
        />
        <Modal
          center
          open={isModalOpen}
          onClose={this.handleCloseModal}
          classNames={{ modal: 'br4 w-100 w-70-l' }}
        >
          <div className="montserrat">
            <div className="f4 fw6 mb4 dark-blue">Minha Agenda</div>
            <div className="br4 ba b--moon-gray">
              <header className="fw6 f5 pa3 near-black flex items-center">
                <span className="fa-layers f3 mr2">
                  <FontAwesomeIcon icon={faCircle} className="gold" />
                  <FontAwesomeIcon
                    icon={faPencilAlt}
                    className="white"
                    transform="shrink-7"
                  />
                </span>
                <span>Cursando Agora</span>
              </header>
              <main className="pa3 pt0 flex flex-wrap">
                {doingClasses.map(code => (
                  <ClasseSlot
                    key={code}
                    code={code}
                    color="dark-blue"
                    forceEnabled
                  />
                ))}
              </main>
              <div className="fw6 f5 gold bt b--light-gray pa3">
                Evolução após cursar
              </div>
              <main className="pa4 pt0">
                <CreditsManager doneClasses={[...doingClasses, ...doneClasses]}>
                  {({
                    scienceOptative,
                    statisticsOptative,
                    mandatoryCredits,
                    electiveCredits,
                    freeCredits,
                  }) => (
                    <div className="flex-l justify-center items-center">
                      <div className="w5">
                        <CreditsProgress
                          mandatoryCredits={mandatoryCredits}
                          electiveCredits={electiveCredits}
                          freeCredits={freeCredits}
                        />
                      </div>
                      <div>
                        <div className="mb4">
                          <SpecialOptatives
                            scienceOptative={scienceOptative}
                            statisticsOptative={statisticsOptative}
                          />
                        </div>
                        <TrackStatus
                          doneClasses={[...doneClasses, ...doingClasses]}
                        />
                      </div>
                    </div>
                  )}
                </CreditsManager>
              </main>
            </div>
            <div className="pv2 bg-moon-gray br--bottom br4 w-90 center mb4" />
            <div className="br4 ba b--moon-gray">
              <header className="fw6 f5 pa3 near-black flex items-center">
                <span className="fa-layers f3 mr2">
                  <FontAwesomeIcon icon={faCircle} className="dark-blue" />
                  <FontAwesomeIcon
                    icon={faClock}
                    className="white"
                    transform="shrink-7"
                  />
                </span>
                <span>Agendadas</span>
              </header>
              <main className="pa3 pt0 flex flex-wrap">
                {scheduledClasses.map(code => (
                  <ClasseSlot
                    key={code}
                    code={code}
                    color="dark-blue"
                    forceEnabled
                  />
                ))}
              </main>
            </div>
            <div className="pv2 bg-moon-gray br--bottom br4 w-90 center mb4" />
          </div>
        </Modal>
      </div>
    )
  }
}

ScheduleManager.propTypes = {
  doneClasses: PropTypes.array.isRequired,
  doingClasses: PropTypes.array.isRequired,
  scheduledClasses: PropTypes.array.isRequired,
}

export default withEvolution(ScheduleManager)
